<template>
  <div class="home_rights">
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <el-table :data="rightsList" style="width: 100%" stripe border>
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column prop="authName" label=""></el-table-column>
        <el-table-column prop="path" label=""></el-table-column>
        <el-table-column label="">
          <template #default="val">
            <el-tag v-if="val.row.level == '0'"></el-tag>
            <el-tag v-else-if="val.row.level == '1'" type="success"></el-tag>
            <el-tag v-else-if="val.row.level == '2'" type="warning"></el-tag>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>

<script>
import { request } from "@/request/index.js";
export default {
  created() {
    this.getRightList();
  },
  data() {
    return {
      // 
      rightsList: [],
    };
  },
  methods: {
    async getRightList() {
      let { data: res } = await request({
        url: "rights/list",
      });

      if (res.meta.status != 200) return this.$message.error("");

      this.rightsList = res.data;
    },
  },
};
</script>

<style lang="scss" scoped></style>
