<template>
  <div class="home_users">
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card class="box-card">
      <!--   -->
      <template v-slot:header class="card_header">
        <el-row :gutter="20">
          <el-col :span="6">
            <el-input
              placeholder=""
              v-model="queryInfo.query"
              clearable
              @clear="getUserList()"
            >
              <el-button slot="append" icon="el-icon-search" @click="getUserList()"></el-button>
            </el-input>
          </el-col>
          <el-col :span="4">
            <el-button type="primary" @click="showDialog = true"></el-button>
          </el-col>
        </el-row>
      </template>
      <el-table :data="userList" border stripe>
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column prop="username" label=""></el-table-column>
        <el-table-column prop="email" label=""></el-table-column>
        <el-table-column prop="mobile" label=""></el-table-column>
        <el-table-column prop="role_name" label=""></el-table-column>
        <el-table-column label="" width="100">
          <!--  -->
          <template #default="val">
            <el-switch v-model="val.row.mg_state" @change="handleSwitchChange(val.row)"></el-switch>
          </template>
        </el-table-column>
        <el-table-column label="" width="200">
          <template #default="val">
            <!--  -->
            <el-button
              size="mini"
              type="primary"
              icon="el-icon-edit"
              @click="handleEdit(val.row.id)"
            ></el-button>
            <!--  -->
            <el-button
              size="mini"
              type="danger"
              icon="el-icon-delete"
              @click="handleDeletUser(val.row.id)"
            ></el-button>
            <!--  -->
            <el-tooltip
              class="item"
              effect="dark"
              content=""
              placement="top"
              :enterable="false"
            >
              <el-button
                @click="handleRoleChange(val.row)"
                size="mini"
                type="warning"
                icon="el-icon-s-tools"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        ref="paginationRef"
        background
        layout="total,sizes,prev, pager, next,jumper"
        :total="total"
        :page-sizes="[1, 2, 5, 10]"
        :page-size.sync="queryInfo.pagesize"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      ></el-pagination>
    </el-card>

    <!--   -->
    <el-dialog
      title=""
      width="30%"
      :visible.sync="showDialog"
      :show-close="false"
      @close="$refs.addUserRef.resetFields()"
    >
      <!-- body -->
      <el-form
        ref="addUserRef"
        status-icon
        :model="addUserForm"
        :rules="addUserRules"
        label-width="80px"
      >
        <el-form-item label="" prop="username">
          <el-input v-model="addUserForm.username"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="addUserForm.password"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="addUserForm.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mobile">
          <el-input v-model="addUserForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <!-- footer -->
      <template #footer class="dialog-footer">
        <el-button @click="showDialog = false"> </el-button>
        <el-button type="primary" @click="handleAddUser"> </el-button>
      </template>
    </el-dialog>

    <!--  -->
    <el-dialog title="" width="30%" :visible.sync="showEditDialog" :show-close="false">
      <!-- body -->
      <el-form
        ref="editUserRef"
        status-icon
        :model="editUserForm"
        :rules="addUserRules"
        label-width="80px"
      >
        <el-form-item label="">
          <el-input disabled v-model="editUserForm.username"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="editUserForm.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mobile">
          <el-input v-model="editUserForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <!-- footer -->
      <template #footer class="dialog-footer">
        <el-button @click="showEditDialog = false"> </el-button>
        <el-button type="primary" @click="handleEditCheck"> </el-button>
      </template>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="showRoleDialog"
      width="30%"
      @close="selectRoleID = null"
    >
      <div class="body">
        <p>{{ activeUser.username }}</p>
        <p>{{ activeUser.role_name }}</p>
        <p>
          
          <el-select v-model="selectRoleID" placeholder="">
            <el-option
              v-for="item in roleList"
              :key="item.id"
              :label="item.roleName"
              :value="item.id"
            ></el-option>
          </el-select>
        </p>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="showRoleDialog = false"> </el-button>
        <el-button type="primary" @click="handleRoleCheck"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
/**
1. 
  -    
  - 
 */
import { request } from "@/request/index.js";
export default {
  created() {
    this.getUserList();
  },
  data() {
    var validateEmail = (rule, value, callback) => {
      const emailReg = /\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*/;
      if (value === "") {
        return callback(new Error(""));
      }
      if (!emailReg.test(value)) {
        return callback(new Error("!"));
      }
      callback();
    };
    var validateMobile = (rule, value, callback) => {
      const mobileReg = /^(0|86|17951)?(13[0-9]|15[012356789]|166|17[3678]|18[0-9]|14[57])[0-9]{8}$/;
      if (value === "") {
        return callback(new Error(""));
      }
      if (!mobileReg.test(value)) {
        return callback(new Error("!"));
      }
      callback();
    };
    return {
      queryInfo: {
        query: "",
        pagenum: 1,
        pagesize: 2,
      },
      userList: [],
      total: 0,
      showDialog: false,
      showEditDialog: false,
      showDeletDialog: false,
      showRoleDialog: false,
      editUserForm: {},
      addUserForm: {
        username: "",
        password: "123456",
        email: "1@qq.com",
        mobile: "15032156234",
      },
      addUserRules: {
        username: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 9, message: " 3  9 ", trigger: "blur" },
        ],
        password: [
          { required: true, message: "", trigger: "blur" },
          { min: 6, max: 9, message: " 6  9 ", trigger: "blur" },
        ],
        email: [{ required: true, validator: validateEmail, trigger: "blur" }],
        mobile: [{ required: true, validator: validateMobile, trigger: "blur" }],
      },
      activeUser: {},
      roleList: [],
      selectRoleID: null,
    };
  },
  methods: {
    async getUserList() {
      let { data: res } = await request({
        url: "users",
        params: this.queryInfo,
      });
      if (res.meta.status !== 200) return this.$message.error("");

      this.total = res.data.total;
      this.userList = res.data.users;
    },
    //  
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize;
      this.getUserList();
    },
    //  
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage;
      this.getUserList();
    },
    //switch 
    async handleSwitchChange(userInfo) {
      let { data: res } = await request({
        url: `users/${userInfo.id}/state/${userInfo.mg_state}`,
        method: "put",
      });

      // switch v-model
      if (res.meta.status !== 200) {
        userInfo.mg_state = !userInfo.mg_state;
        return this.$message.error("");
      }

      this.$message.success("");
    },
    //    
    handleAddUser() {
      this.$refs.addUserRef.validate(async valid => {
        if (!valid) return this.$message.error("");

        let { data: res } = await request({
          url: "users",
          method: "POST",
          data: this.addUserForm,
        });
        if (res.meta.status != 201) return this.$message.error(res.meta.msg);
        this.$message.success("");
        this.showDialog = false;
        this.getUserList();
      });
    },

    // 
    async handleEdit(id) {
      let { data: res } = await request({
        url: "users/" + id,
      });
      if (res.meta.status != 200) return this.$message.error("");

      this.editUserForm = res.data;
      this.showEditDialog = true;
    },
    //  
    handleEditCheck() {
      this.$refs.editUserRef.validate(async valid => {
        if (!valid) return this.$message.error("");

        let { data: res } = await request({
          url: "users/" + this.editUserForm.id,
          method: "PUT",
          data: {
            email: this.editUserForm.email,
            mobile: this.editUserForm.mobile,
          },
        });
        if (res.meta.status != 200) return this.$message.error(res.meta.msg);
        this.$message.success("");
        this.showEditDialog = false;
        this.getUserList();
      });
    },
    // 
    handleDeletUser(userId) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(async () => {
          let { data: res } = await request({
            url: "users/" + userId,
            method: "delete",
          });

          if (res.meta.status != 200) return this.$message.error(res.meta.msg);
          this.$message.success("");
          this.getUserList();
        })
        .catch(() => this.$message.info(""));
    },
    // 
    async handleRoleChange(userInfo) {
      this.showRoleDialog = true;
      this.activeUser.id = userInfo.id;
      this.activeUser.role_name = userInfo.role_name;
      this.activeUser.username = userInfo.username;

      let { data: res } = await request({
        url: "roles",
      });
      if (res.meta.status != 200) return this.$message.error(res.meta.msg);
      this.roleList = res.data;
    },
    async handleRoleCheck() {
      if (this.selectRoleID == null) {
        return this.$message.error("~");
      }
      let { data: res } = await request({
        url: `users/${this.activeUser.id}/role`,
        method: "put",
        data: {
          rid: this.selectRoleID,
        },
      });
      if (res.meta.status != 200) return this.$message.error(res.meta.msg);

      //
      this.$message.success("~");
      this.showRoleDialog = false;
      this.getUserList();
    },
  },
};
</script>

<style lang="scss" scoped>
.el-pagination {
  margin-top: 30px;
}
</style>
