<template>
  <div class="home_roles">
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <template #header>
        <el-button type="primary"></el-button>
      </template>
      <el-table :data="rolesList" style="width: 100%" stripe border>
        <!--  -->
        <el-table-column type="expand">
          <template #default="val">
            <!--  -->
            <el-row
              :class="['bb_line',i ==0?'bt_line':'','mid']"
              v-for="(item,i) in val.row.children"
              :key="item.id"
            >
              <el-col :span="6">
                <el-tag closable @close="handleCloseTag(val.row,item.id)">{{item.authName}}</el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!--  -->
              <el-col :span="18">
                <el-row
                  :class="[i2<item.children.length-1?'bb_line':'','mid']"
                  v-for="(item2,i2) in item.children"
                  :key="item2.id"
                >
                  <el-col :span="6">
                    <el-tag
                      closable
                      type="success"
                      @close="handleCloseTag(val.row,item2.id)"
                    >{{item2.authName}}</el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <!--  -->
                  <el-col :span="18">
                    <el-tag
                      v-for="(item3) in item2.children"
                      :key="item3.id"
                      closable
                      type="warning"
                      @close="handleCloseTag(val.row,item3.id)"
                    >{{item3.authName}}</el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <!--  -->
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column prop="roleName" label=""></el-table-column>
        <el-table-column prop="roleDesc" label=""></el-table-column>
        <el-table-column label="" width="350">
          <template #default="val">
            <!--  -->
            <el-button size="medium" type="primary" icon="el-icon-edit"></el-button>
            <!--  -->
            <el-button size="medium" type="danger" icon="el-icon-delete"></el-button>
            <!--  -->
            <el-button
              @click="showDistriDia(val.row)"
              size="medium"
              type="warning"
              icon="el-icon-s-tools"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>

    <el-dialog @close="checkedKeys=[];" title="" :visible.sync="distriDialog" width="30%">
      <div style="width:100%;height:400px;overflow:auto;">
        <!-- node-key="id"   -->
        <el-tree
          ref="treeRef"
          node-key="id"
          :data="rightList"
          :props="defaultProps"
          show-checkbox
          default-expand-all
          :default-checked-keys="checkedKeys"
        ></el-tree>
      </div>
      <template #footer>
        <el-button @click="distriDialog = false"> </el-button>
        <el-button type="primary" @click="handleRoleAuth"> </el-button>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import { request } from "../../request";
export default {
  created() {
    this.getRoleList();
  },
  data() {
    return {
      rolesList: [],
      distriDialog: false,
      rightList: [],
      defaultProps: {
        children: "children",
        label: "authName"
      },
      checkedKeys: [],
      roleId: 0
    };
  },
  methods: {
    async getRoleList() {
      let { data: res } = await request({
        url: "roles"
      });

      if (res.meta.status != 200) return this.$message.error("");

      this.rolesList = res.data;
    },
    // 
    handleCloseTag(role, rightid) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(async () => {
          let { data: res } = await request({
            url: `roles/${role.id}/rights/${rightid}`,
            method: "delete"
          });
          if (res.meta.status != 200) {
            return this.$message.error(res.meta.msg);
          }
          this.$message.success("");
          role.children = res.data;
        })
        .catch(() => this.$message.info(""));
    },

    // 
    async showDistriDia(node) {
      this.roleId = node.id;
      this.distriDialog = true;
      // 
      let { data: res } = await request({
        url: `rights/tree`,
        method: "get"
      });
      if (res.meta.status != 200) {
        return this.$message.error(res.meta.msg);
      }

      // 
      this.rightList = res.data;
      // 
      this.getCheckedKeys(node, this.checkedKeys);
    },
    // 
    async handleRoleAuth() {
      let tr = this.$refs.treeRef;
      let arr = [...tr.getCheckedKeys(), ...tr.getHalfCheckedKeys()];
      arr = arr.join(",");

      let { data: res } = await request({
        url: `roles/${this.roleId}/rights`,
        method: "post",
        data: {
          rids: arr
        }
      });
      if (res.meta.status != 200) {
        return this.$message.error(res.meta.msg);
      }
      //;

      this.getRoleList();
      this.$message.success("");
      this.distriDialog = false;
    },

    /**
     *   
     *    1.  (children)
     *    2.  children children
     */
    getCheckedKeys(node, arr) {
      //
      if (node.children == null) {
        return arr.push(node.id);
      }

      node.children.forEach(v => {
        this.getCheckedKeys(v, arr);
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.bb_line {
  border-bottom: 1px solid #ddd;
}
.bt_line {
  border-top: 1px solid #ddd;
}
.el-tag {
  margin: 8px;
}

.mid {
  display: flex;
  align-items: center;
}
</style>